.list-header {
  background: $off-white;
  border: 1px solid darken($off-white, 7%);
  color: gray;
  display: flex;
  justify-content: space-between;
  padding: $s-size $m-size;
}

.list-item {
  display: flex;
  flex-direction: column;
  border: 1px solid darken($off-white, 7%);
  border-top: none;
  color: $dark-gray;
  padding: $s-size;
  text-decoration: none;
  transition: background .3s ease;
  &:hover {
      background: $off-white;
  }

  @media (min-width: $desktop-breakpoint) {
      align-items: center;
      flex-direction: row;
      justify-content: space-between;
      padding: $m-size;
  }
}

.list-item__title {
  margin: 0;
  word-break: break-all;
}

.list-item__sub-title {
  color: gray;
  font-size: $font-size-small;
}

.list-item__data {
  margin: $s-size 0 0 0;
  @media (min-width: $desktop-breakpoint) {
      margin: 0;
      padding-left: $s-size;
  }
}

.list-item__message {
  align-items: center;
  color: gray;
  justify-content: center;
  padding: $m-size;
  &:hover {
      background: none;
  }
}

.list-body {
  margin-bottom: $m-size;
  @media (min-width: $desktop-breakpoint) {
      margin-bottom: $l-size;
  }
}